<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'index',
  data () {
    return {
      swiperOption: {
        setWrapperSize :true,
        grabCursor: true,
        speed:500,
        loop: true,
        autoplay: true,
        paginationClickable: true,
        autoplay: {
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          clickable :true,
        },
        on:{
          transitionStart(){
            $('.bannerTabs').removeClass('active')
            if (this.activeIndex%2 == 0) {
              $('.bannerTabs').eq(1).addClass('active');
            }else {
              $('.bannerTabs').eq(0).addClass('active');
            }
          }
        }
      },
      swiperOption2: {
        setWrapperSize :true,
        grabCursor: true,
        speed:500,
        loop: true,
        slidesPerView: 2,
        autoplay: true,
        paginationClickable: true,
        autoplay: {
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination2',
          // dynamicBullets: true,
          clickable :true,
        },
        on:{
          transitionStart(){
            $('.eventTabs').removeClass('active')
            if (this.activeIndex%2 == 0) {
              $('.eventTabs').eq(1).addClass('active');
            }else {
              $('.eventTabs').eq(0).addClass('active');
            }
          }
        }
      },
    }
  },
  methods: {
    changeSwipers(val) {
      let tabs = $('.bannerTabs');
      tabs.removeClass('active');
      tabs.eq(val).addClass('active');
      // this.swiper.slideTo(val,500,false)
      // this.swiper2.slideTo(val,500,false)
    },
  },
  computed: {
    swiper() {
      return this.$refs.bannerSwiper.swiper
    },
    swiper2() {
      return this.$refs.eventSwiper.swiper
    }
  },
}
</script>



